import getV from './getV'

const format = (sheetData) => {
  const end = +sheetData['!ref'].split(':')[1].replace(/\D+/g, '')
  const start = 2
  const result = {}

  for (let i = start; i <= end; i++) {
    let id = getV(sheetData[`C${i}`]),
      name = getV(sheetData[`B${i}`]),
      department = getV(sheetData[`E${i}`]),
      group = getV(sheetData[`F${i}`])

    if (name) {
      name = name.trim()
    }

    result[name] = {
      id,
      name,
      department,
      group
    }
  }

  return result
}

export default format
