const generate = (result3, total, workDays, isCalcWorkDays) => {
  let merges = [
    {
      s: { c: 0, r: 0 },
      e: { c: 18, r: 0 }
    }, {
      s: { c: 0, r: 1 },
      e: { c: 18, r: 1 }
    }
  ]
  let sheet4Merges = []
  let wbData = {}
  const wbData2 = {}
  const wbData3 = {}
  const wbData4 = {}
  let departmentMerge = 3
  let groupMerge = 3
  let no = 1
  let noIndex = 4
  let techCount = 0

  const generateSheet1Data = (item) => {
    wbData[`A${noIndex}`] = {
      v: no,
      t: 'n',
      s: {
        alignment: {
          vertical: 'center',
          horizontal: 'center'
        }
      }
    }

    wbData[`D${noIndex}`] = {
      v: `${item.name}`,
      t: 's',
      s: {
        alignment: {
          vertical: 'center',
          horizontal: 'center'
        }
      }
    }

    wbData[`E${noIndex}`] = {
      v: item.workDays,
      t: 'n',
      s: {
        alignment: {
          vertical: 'center',
          horizontal: 'center'
        }
      }
    }

    wbData[`F${noIndex}`] = {
      v: `${(isCalcWorkDays && item.workDays === workDays) ? '√' : ''}`,
      t: 's',
      s: {
        alignment: {
          vertical: 'center',
          horizontal: 'center'
        }
      }
    }

    if (item.subsidy) {
      wbData[`G${noIndex}`] = {
        v: `${item.subsidy}元`,
        t: 's',
        s: {
          alignment: {
            vertical: 'center',
            horizontal: 'center'
          }
        }
      }
    }

    if (item.overtime) {
      wbData[`H${noIndex}`] = {
        v: `${item.overtime}分钟`,
        t: 's',
        s: {
          alignment: {
            vertical: 'center',
            horizontal: 'center'
          }
        }
      }
    }

    if (item.late) {
      wbData[`I${noIndex}`] = {
        v: `${item.late}分钟`,
        t: 's',
        s: {
          alignment: {
            vertical: 'center',
            horizontal: 'center'
          }
        }
      }
    }
  }
  const createCurrentMonthData = () => {
    const today = new Date()
    const fullYear = today.getFullYear()
    const year = fullYear.toString().slice(-2)
    const month = today.getMonth() + 1;
    const fullMonth = month < 10 ? `0${month}` : month
    const last = (new Date(year, month, 0)).getDate()

    return {
      fullYear,
      year,
      fullMonth,
      month,
      last
    }
  }
  const createLabelName = n => {
    let s = ''
    let number = parseInt(n)
    while (number > 0) {
      let m = number % 26

      if (m == 0) {
        m = 26
      }

      s = String.fromCharCode(m + 64) + s
      number = (number - m) / 26
    }
    return s
  }
  const { year, month, last: lastDay, fullMonth, fullYear } = createCurrentMonthData()
  const sheet2LastLabel = createLabelName(lastDay + 3)
  const generateSheet2Data = item => {
    const index = noIndex - 1

    wbData2[`A${index}`] = {
      v: `${item.name}`,
      t: 's',
      s: {
        alignment: {
          vertical: 'center',
          horizontal: 'center'
        },
        fill: {
          fgColor: {
            rgb: 'FF9FCD63'
          }
        }
      }
    }

    if (item.late != null) {
      wbData2[`${sheet2LastLabel}${index}`] = {
        v: item.late,
        t: 'n',
        s: {
          alignment: {
            vertical: 'center',
            horizontal: 'center'
          }
        }
      }
    }

    Object.entries(item.data).forEach(([time, { late }]) => {
      const date = +time.split('/')[2]

      if (late != null) {
        wbData2[`${createLabelName(date + 1)}${index}`] = {
          v: late,
          t: 'n',
          s: {
            alignment: {
              vertical: 'center',
              horizontal: 'center'
            }
          }
        }
      }
    })
  }
  const generateSheet3Data = item => {
    const index = noIndex - 1

    wbData3[`A${index}`] = {
      v: `${item.name}`,
      t: 's',
      s: {
        alignment: {
          vertical: 'center',
          horizontal: 'center'
        },
        fill: {
          fgColor: {
            rgb: 'FF9FCD63'
          }
        }
      }
    }
  }
  const generateSheet4Data = item => {
    const no = techCount + 1
    const index = techCount + 6

    wbData4[`A${index}`] = {
      v: no,
      t: 'n',
      s: {
        alignment: {
          horizontal: 'center'
        }
      }
    }

    wbData4[`B${index}`] = {
      v: `${item.name}`,
      t: 's',
      s: {
        alignment: {
          horizontal: 'center'
        }
      }
    }

    sheet4Merges.push({
      s: { c: 1, r: techCount + 5 },
      e: { c: 2, r: techCount + 5 }
    })

    wbData4[`${createLabelName(lastDay + 4)}${index}`] = {
      v: parseFloat(`${(item.overtime / 60).toFixed(1)}`),
      t: 'n',
      s: {
        alignment: {
          horizontal: 'center'
        }
      }
    }

    if (item.subsidy > 0) {
      wbData4[`${createLabelName(lastDay + 5)}${index}`] = {
        v: `${(item.subsidy).toFixed(0)}元`,
        t: 's',
        s: {
          alignment: {
            horizontal: 'center'
          }
        }
      }
    }

    Object.entries(item.data).forEach(([time, { overtime }]) => {
      const date = +time.split('/')[2]

      if (overtime != null && overtime > 0) {
        let s = {
          s: {
            alignment: {
              horizontal: 'center'
            }
          }
        }

        if (overtime <= 60) {
          s.s.font = {
            color: {
              rgb: 'FFFF0000'
            }
          }
        }

        wbData4[`${createLabelName(date + 3)}${index}`] = {
          v: parseFloat(`${(overtime / 60).toFixed(1)}`),
          t: 'n',
          ...s
        }
      }
    })
  }

  for (let department in result3) {
    for (let key in result3[department]) {
      let value = result3[department][key]

      if (key == 'count') {
        merges.push(
          {
            s: { c: 1, r: departmentMerge },
            e: { c: 1, r: departmentMerge + value - 1 }
          }
        )

        wbData[`B${departmentMerge + 1}`] = {
          v: department,
          t: 's',
          s: {
            alignment: {
              vertical: 'center',
              horizontal: 'center'
            }
          }
        }
        departmentMerge += value
      } else {
        let group = key

        for (let key2 in value) {
          let value2 = value[key2]

          if (key2 === 'count') {
            merges.push(
              {
                s: { c: 2, r: groupMerge },
                e: { c: 2, r: groupMerge + value2 - 1 }
              }
            )

            wbData[`C${groupMerge + 1}`] = {
              v: group,
              t: 's',
              s: {
                alignment: {
                  vertical: 'center',
                  horizontal: 'center'
                }
              }
            }
            groupMerge += value2
          } else {
            generateSheet1Data(value2)
            generateSheet2Data(value2)
            generateSheet3Data(value2)

            if (department == '技术部') {
              generateSheet4Data(value2)
              techCount++
            }

            no++
            noIndex++
          }
        }
      }
    }
  }

  for (let i = 1; i <= lastDay; i++) {
    wbData2[`${createLabelName(i + 1)}2`] = {
      v: `${year}.${month}.${i}`,
      t: 's',
      s: {
        alignment: {
          vertical: 'center',
          horizontal: 'center'
        }
      }
    }

    wbData3[`${createLabelName(i + 1)}2`] = {
      v: `${year}.${month}.${i}`,
      t: 's',
      s: {
        alignment: {
          vertical: 'center',
          horizontal: 'center'
        }
      }
    }

    wbData4[`${createLabelName(i + 3)}5`] = {
      v: `${i}日`,
      t: 's',
      s: {
        alignment: {
          horizontal: 'center'
        }
      }
    }
  }

  wbData2[`${sheet2LastLabel}2`] = {
    v: '总计（分钟）',
    t: 's',
    s: {
      alignment: {
        vertical: 'center',
        horizontal: 'center'
      }
    }
  }
  wbData3[`${sheet2LastLabel}2`] = {
    v: '总计（小时）',
    t: 's',
    s: {
      alignment: {
        vertical: 'center',
        horizontal: 'center'
      }
    }
  }

  merges.push({
    s: { c: 0, r: total + 3 },
    e: { c: 18, r: total + 3 }
  });

  let sheet1Cols = []
  for (let i = 1; i <= 19; i++) {
    sheet1Cols.push({
      wpx: 95
    })
  }

  const wb = {
    SheetNames: ['汇总', '明细', '休病假'],
    Sheets: {
      '汇总': {
        '!ref': `A1:S${total + 4}`,
        '!merges': merges,
        '!cols': sheet1Cols,
        ...wbData,
        A1: {
          v: `广州汉风科技有限公司考勤表（${month}月份）`,
          t: 's',
          s: {
            alignment: {
              vertical: 'center',
              horizontal: 'center'
            },
            font: {
              sz: '18'
            }
          }
        },
        A2: {
          v: `              负责人签字:                       ${fullYear}/${month}/${lastDay}`,
          t: 's',
          s: {
            alignment: {
              horizontal: 'top'
            },
            font: {
              sz: '10'
            }
          }
        },
        A3: {
          v: '序号',
          t: 's',
          s: {
            alignment: {
              vertical: 'center',
              horizontal: 'center'
            }
          }
        },
        [`A${total + 4}`]: {
          v: '备注：每月每人100元全勤奖励（已经走邮件批复）。',
          t: 's',
          s: {
            alignment: {
              vertical: 'center',
            }
          }
        },
        B3: {
          v: '部门',
          t: 's',
          s: {
            alignment: {
              vertical: 'center',
              horizontal: 'center'
            }
          }
        },
        C3: {
          v: '组别',
          t: 's',
          s: {
            alignment: {
              vertical: 'center',
              horizontal: 'center'
            }
          }
        },
        D3: {
          v: '姓名',
          t: 's',
          s: {
            alignment: {
              vertical: 'center',
              horizontal: 'center'
            }
          }
        },
        E3: {
          v: '出勤',
          t: 's',
          s: {
            alignment: {
              vertical: 'center',
              horizontal: 'center'
            }
          }
        },
        F3: {
          v: '全勤',
          t: 's',
          s: {
            alignment: {
              vertical: 'center',
              horizontal: 'center'
            }
          }
        },
        G3: {
          v: '餐补',
          t: 's',
          s: {
            alignment: {
              vertical: 'center',
              horizontal: 'center'
            }
          }
        },
        H3: {
          v: '加班',
          t: 's',
          s: {
            alignment: {
              vertical: 'center',
              horizontal: 'center'
            }
          }
        },
        I3: {
          v: `${month}月累计迟到`,
          t: 's',
          s: {
            alignment: {
              vertical: 'center',
              horizontal: 'center'
            }
          }
        },
        J3: {
          v: '上班未打卡',
          t: 's',
          s: {
            alignment: {
              vertical: 'center',
              horizontal: 'center'
            }
          }
        },
        K3: {
          v: `${month}月累计早退`,
          t: 's',
          s: {
            alignment: {
              vertical: 'center',
              horizontal: 'center'
            }
          }
        },
        L3: {
          v: '下班未打卡',
          t: 's',
          s: {
            alignment: {
              vertical: 'center',
              horizontal: 'center'
            }
          }
        },
        M3: {
          v: `${month}月累计旷工`,
          t: 's',
          s: {
            alignment: {
              vertical: 'center',
              horizontal: 'center'
            }
          }
        },
        N3: {
          v: `${month}月累计事假`,
          t: 's',
          s: {
            alignment: {
              vertical: 'center',
              horizontal: 'center'
            }
          }
        },
        O3: {
          v: `${month}月累计病假`,
          t: 's',
          s: {
            alignment: {
              vertical: 'center',
              horizontal: 'center'
            }
          }
        },
        P3: {
          v: `${month}月累计年假`,
          t: 's',
          s: {
            alignment: {
              vertical: 'center',
              horizontal: 'center'
            }
          }
        },
        Q3: {
          v: `${month}月累计有薪假`,
          t: 's',
          s: {
            alignment: {
              vertical: 'center',
              horizontal: 'center'
            }
          }
        },
        R3: {
          v: '备注',
          t: 's',
          s: {
            alignment: {
              vertical: 'center',
              horizontal: 'center'
            }
          }
        },
        S3: {
          v: '员工签名',
          t: 's',
          s: {
            alignment: {
              vertical: 'center',
              horizontal: 'center'
            }
          }
        }
      },

      '明细': {
        '!ref': `A1:${sheet2LastLabel}${total + 2}`,
        '!merges': [
          {
            s: { c: 0, r: 0 },
            e: { c: lastDay + 2, r: 0 }
          }
        ],
        A1: {
          v: '广州汉风员工迟到登记表',
          t: 's',
          s: {
            alignment: {
              vertical: 'center',
              horizontal: 'center'
            }
          }
        },
        ...wbData2
      },

      '休病假': {
        '!ref': `A1:${sheet2LastLabel}${total + 2}`,
        '!merges': [
          {
            s: { c: 0, r: 0 },
            e: { c: lastDay + 2, r: 0 }
          }
        ],
        A1: {
          v: '广州汉风员工休病假登记表',
          t: 's',
          s: {
            alignment: {
              vertical: 'center',
              horizontal: 'center'
            }
          }
        },
        ...wbData3
      }
    }
  }

  let sheet4Cols = []
  for (let i = 1; i <= lastDay + 5; i++) {
    let wpx = i >= lastDay + 4 ? 90 : 40

    sheet4Cols.push({
      wpx
    })
  }

  const wb2 = {
    SheetNames: ['Sheet1'],
    Sheets: {
      'Sheet1': {
        '!ref': `A1:${createLabelName(lastDay + 5)}${techCount + 14}`,
        '!cols': sheet4Cols,
        '!merges': [
          {
            s: { c: 0, r: 0 },
            e: { c: lastDay + 4, r: 1 }
          },
          {
            s: { c: 0, r: 2 },
            e: { c: 12, r: 2 }
          },
          {
            s: { c: 13, r: 2 },
            e: { c: lastDay - 1, r: 2 }
          },
          {
            s: { c: 3, r: 3 },
            e: { c: lastDay + 4, r: 3 }
          },
          {
            s: { c: lastDay, r: 2 },
            e: { c: lastDay + 4, r: 2 }
          },
          {
            s: { c: 0, r: techCount + 5 },
            e: { c: lastDay + 4, r: techCount + 7 }
          },
          {
            s: { c: lastDay + 3, r: techCount + 8 },
            e: { c: lastDay + 4, r: techCount + 10 }
          },
          {
            s: { c: 0, r: techCount + 8 },
            e: { c: lastDay + 2, r: techCount + 10 }
          },
          {
            s: { c: 0, r: techCount + 11 },
            e: { c: 12, r: techCount + 13 }
          },
          {
            s: { c: lastDay, r: techCount + 11 },
            e: { c: lastDay + 4, r: techCount + 13 }
          },
          {
            s: { c: 13, r: techCount + 11 },
            e: { c: lastDay - 1, r: techCount + 13 }
          },
          ...sheet4Merges
        ],
        'A1': {
          v: '加班申请表',
          t: 's',
          s: {
            alignment: {
              vertical: 'center',
              horizontal: 'center'
            }
          }
        },
        'A3': {
          v: '申请部门：技术部',
          t: 's'
        },
        'N3': {
          v: '申请人：李冬杰',
          t: 's'
        },
        [`${createLabelName(lastDay + 1)}3`]: {
          v: `申请日期：${fullYear}${fullMonth}01-${fullMonth}${lastDay - 1}`,
          t: 's',
          s: {
            alignment: {
              horizontal: 'center'
            }
          }
        },
        'D4': {
          v: `${fullYear}年${month}月`,
          t: 's',
          s: {
            alignment: {
              horizontal: 'center'
            }
          }
        },
        [`${createLabelName(lastDay + 4)}5`]: {
          v: '合计（小时）',
          t: 's',
          s: {
            alignment: {
              horizontal: 'center'
            }
          }
        },
        [`${createLabelName(lastDay + 5)}5`]: {
          v: '餐补（元）',
          t: 's',
          s: {
            alignment: {
              horizontal: 'center'
            }
          }
        },
        [`A${techCount + 6}`]: {
          v: '加班原因（具体工作内容）：',
          t: 's',
          s: {
            alignment: {
              vertical: 'top'
            }
          }
        },
        [`${createLabelName(lastDay + 4)}${techCount + 9}`]: {
          v: '申请人签字：  ',
          t: 's',
          s: {
            alignment: {
              vertical: 'top'
            }
          }
        },
        [`A${techCount + 12}`]: {
          v: '部门负责人审批：                    ',
          t: 's',
          s: {
            alignment: {
              vertical: 'top'
            }
          }
        },
        [`N${techCount + 12}`]: {
          v: '行政部：                 ',
          t: 's',
          s: {
            alignment: {
              vertical: 'top'
            }
          }
        },
        [`${createLabelName(lastDay + 1)}${techCount + 12}`]: {
          v: '总经理：                    ',
          t: 's',
          s: {
            alignment: {
              vertical: 'top'
            }
          }
        },
        ...wbData4
      }
    }
  }

  return {
    sheet1: wb,
    sheet2: wb2
  }
}

export default generate
