const getDepartment = data => {
  return data ? data.department : ''
}
const getGroup = data => {
  return data ? data.group : ''
}

const format = (result, result2) => {
  let result3 = {}
  let total = 0

  for (let id in result) {
    const name = result[id].name;
    const department = getDepartment(result2[name])
    let group = getGroup(result2[name])
    group = group || department

    result[id].department = department
    result[id].group = group

    if (department === '技术部') {
      for (let date in result[id].data) {
        let subsidy = result[id].data[date]._subsidy
        let subsidyTime = result[id].data[date]._subsidyTime

        if (subsidy > 0) {
          result[id].data[date].subsidy = subsidy
          result[id].data[date].subsidyTime = subsidyTime
          result[id].subsidy += subsidy
          result[id].subsidyTime += subsidyTime
        }
      }
    } else {
      result[id].overtime = 0;
    }

    if (!result3[department]) {
      result3[department] = {
        count: 0
      }
    }

    if (!result3[department][group]) {
      result3[department][group] = {
        count: 0
      }
    }

    result3[department][group][id] = result[id]
    result3[department].count++
    result3[department][group].count++
    total++
  }

  return {
    total,
    data: result3
  }
}

export default format
